body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}
html,
body,
div#root {
  height: 100%;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  margin-left: 0;
  padding-left: 0;
}

ul {
  margin-left: 0;
  padding-left: 0;
}

a,
h1,
h2,
h3,
h4,
h5,
h6,
i {
  color: #8c45cf;
  text-decoration: none;
}

h2 {
  font-size: 1rem;
  font-weight: 500;
  padding: 0.2rem;
}

nav {
  padding: 0;
  max-height: 3rem;
  display: flex;
  /* background-color: #8c45cf; */
}

.details-pane .provider-info {
  margin: 0 0 10px 0;
  overflow: hidden;
  padding: 5px 0;
}

.details-pane .provider-details-info,
.details-pane .popup-text {
  padding-left: 5px;
}

.details-pane .missions {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 15px;
  padding: 10px 0px 10px 0px;
}

.details-pane .missions.expanded {
  white-space: normal;
  white-space: initial;
}

.details-pane .missions-expander {
  float: right;
  font-weight: bold;
  font-size: 8px;
  color: royalblue;
  cursor: pointer;
}

.provider-details-info {
  display: flex;
  font-size: 10px;
  min-width: 0px;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 50%;
  margin: 5px;
}

.provider-details-info .ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.provider-details-info .provider-details-icon {
  margin-right: 0.5em;
}

.provider-details-info .providers-detail-content {
  color: royalblue;
  text-overflow: ellipsis;
  overflow: hidden;
}

.provider-details-info .providers-detail-content a:link {
  color: royalblue;
}

.map {
  width: 100%;
  height: 100vh;
  z-index: 0;
}

.map-pin-container {
  -webkit-transform-origin: center bottom;
          transform-origin: center bottom;
  -webkit-transform: translateY(-45%);
          transform: translateY(-45%);
}
.map-pin {
  background-color: #8c45cf;
  height: 50px;
  width: 50px;
  border-radius: 20% 50% 100% 50%;
  -webkit-transform: scale(0.8, 1) rotate(225deg);
          transform: scale(0.8, 1) rotate(225deg);
}

.pin-hole {
  background-color: white;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  -webkit-transform: translate(18px, -32px);
          transform: translate(18px, -32px);
}

.side-menu {
  position: absolute;
  z-index: 1;
  width: 400px;
  height: calc(100vh - 2em); /* TODO this should be flex box column */
  margin: 15px;
  margin-top: 100px;
  -webkit-filter: drop-shadow(0 3px 3px gray);
          filter: drop-shadow(0 3px 3px gray);
}
.react-tabs__tab-list { display: flex; }
.react-tabs__tab {
  border: none;
  background: #aaa;
  color: white;
  border-radius: 15px 15px 0 0;
  flex: 1 1 auto;
}
.react-tabs__tab--selected {
  background: white;
  border: none;
}
.react-tabs__tab h3 {
  margin: 8px 20px;
  text-transform: uppercase;
  color: white;
}
.react-tabs__tab--selected h3 {
  color: #8c45cf;
}

/* .panels */
.side-menu > div {
  margin-top: -10px; /* counter react-tabs styling */
  padding: 4px 16px;

  max-height: 100%;

  background-color: white;

  overflow-y: scroll;
}

.expandable-container {
  /* width: 100%; */
  color: #8c45cf;
  overflow: visible;
  position: relative;
  background: #FDFAFF;
  z-index: 100;
}

.expandable-container .expanded {
  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
}

.expandable-container .expandable-content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  padding: 0 1rem 1rem 1rem;
  border-radius: 8px;
  border: 1px solid #D7ADFF;
  box-shadow: 1px 3px 4px rgba(96, 1, 185, 0.25);
}

.expandable-container .expandable-content-wrapper.expanded {
  height: auto;
}

.expandable-container .expandable-header {
  padding: 0 5px;
  height: 100%;
}

.expandable-container .dropdown-input-wrapper {
  display: flex;
  justify-content: flex-start;
  margin-top: 0.5rem;
}

.expandable-container .expanded-content {
  padding-top: 6px;
  height: 0px;
  opacity: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  pointer-events: none;
}

.expandable-container .expanded-content.expanded {
  height: auto;
  max-height: 300px;
  opacity: 1;
  pointer-events: auto;
}

.sort-container {
    align-items: center;
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    position: relative;
}
.sort-container .expandable-container {
    flex: 1 1 auto;
    margin-right: 1rem;
}
.side-menu .expandable-container .expanded-content {
    padding: 0;
    position: absolute;
    top: 100%;
}
.side-menu .expandable-content .expandable-header {
    padding: 0;
}
.side-menu .expandable-header {
    padding: 0.75rem;
    text-transform: uppercase;
}
.side-menu .expandable-header h4 {
    margin: 0;
    padding: 0;
}
.expandable-content-wrapper.sort-by {
    box-shadow: 1px 1px 2px #aaa;
    flex: 1 1 auto;
    padding: 0;
    cursor: pointer;
}

.radio-container {
    color: gray;
    padding: 0.75rem;
}
.radio-container:hover {
    background-color: #d2e9ff;
}
.radio-container.selected {
    background-color: #bedefd;
}
.radio-container input {
    display: none;
}
.radio-container label {
    margin: 0;
    cursor: pointer;
}
h2 {
  margin: 2px 2px 0 0;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.4px;
}

p {
  margin: 2px 0 6px 2px;
  font-size: 14px;
  line-height: 12px;
  font-weight: bold;
  color: gray;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
}

label {
  margin-left: 10px;
}

.dropdown-list-container.expanded p {
  color: white;
}

.top-nav {
  /* display: flex; */
  overflow: visible;
  height: 10%;
  background-color: white;
  z-index: 8;
}

.nav-search {
  margin-top: 5px;
}

.upper-bar {
  position: absolute;
  z-index: 20;
  background-color: white;
  border-bottom: 1px solid #8c45cf;
  width: 100%;
}

.dropdown-row {
  display: flex;
  width: 100%;
  padding: 0 15px;
}

.dropdown-list-container {
  flex: 1 1;
  margin: 0 8px;
  cursor: pointer;
  z-index: 10;
  background-color: white;
  border: 1px solid #8c45cf;
  box-shadow: 2px 1px 2px #8c45cf;
}

.placeholder {
  box-shadow: none;
  border: none;
}

.dropdown-list {
  display: flex;
  flex-direction: column;
  height: 0;
  /* max-height: 100%; */
  /* width: 100%; */
  padding-top: 0px;
  text-align: left;
  opacity: 0;
  background-color: white;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.dropdown-list-container.expanded .dropdown-list {
  flex: 1 1; 
  padding-top: 6px;
  height: auto;
  opacity: 1;
  width: 100%; 
}

.dropdown-list-item {
  height: 0px;
  padding: 0px 5px;
  color: black;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}
.dropdown-list-container.expanded .dropdown-list-item {
  height: 30px;
  padding: 2px 5px;
}

.dropdown-list-header {
  width: 100%;
  padding: 0 5px;
  color: #8c45cf;
  background-color: white;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.dropdown-list-container.expanded .dropdown-list-header {
  background-color: #8c45cf;
}
.dropdown-list-container.expanded .dropdown-list-header h2 {
  color: white;
}

.dropdown-button {
  height: 0;
  /* max-height: 100%;  */
  width: 100%;
  margin: 0 24px;
  padding: 0 10px;
  border-radius: 0;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.dropdown-list-container.expanded .dropdown-button {
  border: 1px solid #8c45cf;
  height: 100%;
  margin: 12px 24px;
  padding: 10px;
}

.dropdown-menu {
  cursor: pointer;
  color: gray;
  font-weight: bold;
  margin: 7px 2px;
}

.dropdown-menu.expanded {
  color: #8c45cf;
}


