.provider-card {
  display: flex;
  width: 100%;
  flex-flow: column nowrap;
  padding: 10px;
  margin-top: 1em;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.provider-card.savedHighlighted {
  background-color: #faf7fe;
}

.provider-card.unchanged {
  background-color: #f5fbff;
}

.provider-card .card-container {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-right: 80px;
}

.card-header {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.card-header h5 {
  margin: 0 0 5px 0;
  font-size: 16px;
  color: #000;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
}

.wrapped-info {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.prov-type {
  height: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #538200;
}

.prov-type.wrapped {
  width: 100px;
}
.prov-type.expanded {
  width: 180px;
}

.prov-type p {
  font-size: 10px;
  margin: 5px 0;
  text-transform: uppercase;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.wrapped-info svg:first-of-type {
  margin-right: 5px;
}
.wrapped-info .wrapped-icons svg {
  font-size: 12px;
  margin: 3px;
}

.wrapped-info:last-child {
  min-width: 60px;
  font-size: 12px;
  color: #7e7e7e;
}

.wrapped-icons {
  color: #7e7e7e;
}

.save-button-container {
  position: absolute;
  top: 5px;
  right: 0;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 70px;
  border: 0;
  cursor: pointer;
  outline: none;
}

.remoteButton {
  border: none;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 40px;
  width: 70px;
  cursor: pointer;
  background-color: transparent;
  font-size: 12px;
  font-weight: bold;
  color: red;
}

.remoteButton > svg {
  font-size: 20px;
}

.button.saved {
  background-color: #70b5ee;
  border-radius: 5px;
  color: #fff;
}

.button.unsaved {
  background-color: #2699fb;
  border-radius: 5px;
  color: #fff;
}

.button > svg {
  font-size: 25px;
}

.button.unsaved > svg {
  margin-right: 5px;
}

.button.saved > svg {
  margin-bottom: 5px;
}

.search-center {
    color:#7e7e7e;
    font-size:12px;
}
.saved-list header {
  display: flex;
  justify-content: space-between;
}
.saved-list h3 {
  color: gray;
  color: #8c45cf;
  font-weight: bold;
  margin: 7px 2px;
}

.remove-button {
  color: #333;
  display: flex;
  flex-direction: row-reverse;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
}

.top-bar {
  overflow: visible;
  z-index: 8;
  border-bottom: 1px solid #8c45cf;
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: stretch;
  height: 70px;
}

.top-bar > * {
  flex: 1 1;
}

.top-bar .top-bar-item {
  background-color: #FDFAFF;
  border: 1px solid #D7ADFF;
  box-shadow: 1px 3px 4px rgba(96, 1, 185, 0.25);
  margin: 0 8px;
  cursor: pointer;
  height: 100%;
  border-radius: 8px;
}

.mapboxgl-ctrl-geocoder {
  height: 100%;
  border-radius: 0px;
}

.dropdown-row {
  display: flex;
  margin-top: 0;
}

.nav-search {
  margin-top: 5px;
}

.mapboxgl-ctrl-geocoder {
  border-radius: 8px !important;
}

#nav-search .msm-map-search-box {
  box-shadow: none !important;
  width: 100% !important;
}

.dropdown-row ul {
  display: flex;
  flex-direction: row;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.flex-container {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-items: flex-start;
}

.section-header {
  display: flex;
  align-items: center;
  justify-items: flex-end;
  width: 100%;
  flex: 1 1;
}

.section-header a,
.section-header i {
  padding: 0.3rem;
}

.section-header:hover {
  background-color: #8c45cf;
}

.section-header:hover a,
.section-header:hover i {
  color: white;
}

.map-container {
  position: relative;
  /* display: flex;
  flex-direction: row;
  width: 100%; */
}

.child-menu {
  display: none;
  max-height: 380px;
  overflow: scroll;
}

.child-menu.show {
  display: block;
}

.DropdownMenu {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 0.5rem;
}

.DropdownMenu span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.popup {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  width: 320px;
  max-height: 320px;
  padding: 0 16px 0 16px;
}

.popup > div {
  margin-top: 1rem;
}

.popup-title {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #9778b7;
  font-size: 22px;
  font-weight: 800;
  text-transform: uppercase;
}

.popup-text {
  font-size: 16px;
  font-weight: 400;
  color: #909090;
  max-height: 6em;
  overflow: scroll;
}

.bottom-button-bar {
  flex: 1 1;
  display: flex;
  flex-direction: row-reverse;
  align-self: flex-end;
  justify-content: space-between;
  color: #2699fb;
  text-transform: uppercase;
  font-weight: bolder;
  background-color: white;
}
.popup .bottom-button-bar {
  width: 320px;
}

.statusToggles {
  cursor: pointer;
  display: flex;
}

